

















































import appConfig from "@/service/app.config";
import { KakaoUserMe, UserModel } from "@/x-vue/interfaces/interfaces";
import { ApiService } from "@/x-vue/services/api.service";
import ChatBubbleSvg from "@/x-vue/svg/ChatBubbleSvg.vue";
import GoogleLogoSvg from "@/x-vue/svg/GoogleLogoSvg.vue";
import FacebookLogoSvg from "@/x-vue/svg/FacebookLogoSvg.vue";

import Vue from "vue";
import Component from "vue-class-component";

import firebase from "firebase/app";
import "firebase/auth";
import { AppService } from "@/service/app.service";

// eslint-disable-next-line  @typescript-eslint/no-explicit-any
declare const window: any;

@Component({
  components: { ChatBubbleSvg, GoogleLogoSvg, FacebookLogoSvg },
})
export default class extends Vue {
  app = AppService.instance;
  api = ApiService.instance;
  form = {};
  initKakao = false;

  async onSubmit(): Promise<void> {
    console.log("Login::form", this.form);
    try {
      await this.app.login(this.form);
      await AppService.instance.loginOrRegisterIntoFirebase();
    } catch (e) {
      console.log(e);
      this.app.error(e);
    }
  }

  loginWithKakao(): void {
    const Kakao = window.Kakao; // Kakao SDK 객체
    if (this.initKakao === false) {
      Kakao.init(appConfig.kakaoJavascriptKey); // SDK 객체를 최초 1회만 초기화
      this.initKakao = true;
    }
    Kakao.Auth.login({
      // Kakao 로그인
      success: (authObj: unknown) => {
        console.log(authObj);
        Kakao.API.request({
          // Kakao 회원 정보 요청
          url: "/v2/user/me",
          data: { secure_resource: true }, // 프로필 사진을 HTTPS 로 가져 옴
          success: async (me: KakaoUserMe) => {
            // 변수에 담아서, PHP 로 전달 할 정보
            const data = {
              id: me.id,
              nickname: me.kakao_account?.profile?.nickname ?? "",
              photoUrl: me.kakao_account?.profile?.profile_image_url ?? "",
              domain: location.hostname,
            };
            console.log("kakaoLogin request data;", data);
            // Centerx API 로 회원 로그인(또는 최초 1회 회원 가입)
            const user: UserModel = await this.api.kakaoLogin(data);
            console.log("kakaoLogin response data; ", user);

            await AppService.instance.loginOrRegisterIntoFirebase();
          },
          fail: (error: unknown) => {
            alert(error);
          },
        });
      },
      fail: (err: unknown) => {
        alert(JSON.stringify(err));
      },
    });
  }
  async loginWithGoogle(): Promise<void> {
    try {
      const provider = new firebase.auth.GoogleAuthProvider();
      const auth = firebase.auth();
      auth.languageCode = "ko";
      const res = await auth.signInWithPopup(provider);

      if (res.user) {
        const user = res.user;
        const data = {
          firebaseUid: user.uid,
          email: user.email,
          nickname: user.displayName,
          photoUrl: user.photoURL,
          domain: location.hostname,
          provider: "google",
        };
        const backendUser = await this.api.firebaseLogin(data);
        console.log("backend user", backendUser);
      } else {
        this.app.error("error_google_login_user_empty");
      }
    } catch (e) {
      this.app.error(e.message);
    }
  }
  async loginWithFacebook(): Promise<void> {
    try {
      const provider = new firebase.auth.FacebookAuthProvider();
      const auth = firebase.auth();
      auth.languageCode = "ko";
      const res = await auth.signInWithPopup(provider);

      if (res.user) {
        const user = res.user;
        const data = {
          firebaseUid: user.uid,
          email: user.email,
          nickname: user.displayName,
          photoUrl: user.photoURL,
          domain: location.hostname,
          provider: "facebook",
        };
        const backendUser = await this.api.firebaseLogin(data);
        console.log("backend user", backendUser);
      } else {
        this.app.error("error_facebook_login_user_empty");
      }
    } catch (e) {
      console.log("e;", e);
      this.app.error(e);
    }
  }
}
